    <pre>
      function isEmpty(a){
        /* 检验空字符串 */  
        if (a === "") return true; 
        /* 检验字符串类型的null */  
        if (a === "null") return true; 
        /* 检验字符串类型的undefined */
        if (a === "undefined") return true; 
        /* 检验 undefined 和 null */  
        if (!a && a !== 0 && a !=="") return true;
        /* 检验空数组 */  
        if (Array.prototype.isPrototypeOf(a) && a.length === 0 ) return true;
        /* 检验空对象  */  
        if (Object.prototype.isPrototypeOf(a) && Object.keys(a).length === 0 ) return true;  
        return false;
      }
    </pre>
    <a href="https://www.cnblogs.com/Scooby/p/13523855.html">转：JavaScript 判断空对象、空数组的方法</a>
